@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css);
body {
  background: #f6f6f6;
}
.App {
    width: 100%;
}
header {
    top: 0;
    left:  0;
    width: 100%;
    background: #fff;
    height: 80px;
    position: fixed;
    border-bottom: 1px solid rgba(0,0,0,.125);
    box-shadow: 1px 4px 10px -3px rgba(0,0,0,0.49);
    -webkit-box-shadow: 1px 4px 10px -3px rgba(0,0,0,0.49);
    -moz-box-shadow: 1px 4px 10px -3px rgba(0,0,0,0.49);
    z-index: 999;
}
.splitMenuBtn { 
    border: none;
    background: transparent;
    outline: none !important;
}
.splitMenuBtn:hover i {
    color: rgb(0, 152, 179);
}

.splitMenuBtn i { 
    font-size: 24px;
}
.fillSizeBtn {
    border: none;
    background: transparent;
    outline: none !important;
}
.fillSizeBtn:active {
    transition: 0.15ms;
    -webkit-transition: 0.15ms;
    -moz-transition: 0.15ms;
    -ms-transition: 0.15ms;
    -o-transition: 0.15ms;
}
.fillSizeBtn:hover i {
    color: rgb(0, 152, 179);
}
.fillSizeBtn i {
    font-size: 24px;
}
.popup-item {
    position: relative;
    margin-right: 10px;
}
.popup-div {
    display: none;
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 2px;
    background: #fff;
    z-index: 99;
    max-height: 350px;
    position: absolute;
    width: 300px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    top: 50px;
    left: 0;
    overflow-x: hidden;
}
.popup-div img {
    width: 50px;
    height: 60px;
    position: relative;
}
.delta {
    position: relative;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
.delta:after {
    content: '';
    position: absolute;
    left: 20px;
    bottom: 0px;
    border: 10px solid transparent;
    border-bottom: 10px solid #f0f0f0;
}
.delta:before {
    content: '';
    position: absolute;
    left: 20px;
    bottom: 20px;
}
.cartL {
    top: 2px !important;
    font-size: 10px;
}
.cartL:active {
    top: -5px !important;
}
.cartL:focus {
    top: -10px !important;
}
.rever:hover {
    color: #0d6efd;
}
.miniCart {
    margin-top: 5px;
    align-items: center;
}
.miniCart h5 {
    margin-bottom: 0 !important;
}
.miniCart p {
    margin-bottom: 0 !important;
}
.miniCart div {
    padding-left: 10px;
}
.input-group {
    width: 350px;
    justify-content: flex-end;
}
.input-group input {
    max-width: 300px;
}
.dashboard_block {
    display: grid;
    grid-template-columns: 90px 100px;
}
.dashboard {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 90px;
    background: #fff;
    height: calc(100% - 80px);
    border-right: 1px solid rgba(0,0,0,.125);
    box-shadow: 6px 0px 10px -3px rgba(0,0,0,0.49);
    -webkit-box-shadow: 6px 0px 10px -3px rgba(0,0,0,0.49);
    -moz-box-shadow: 6px 0px 10px -3px rgba(0,0,0,0.49);
    border-radius: 0 5px 5px 0;
    -webkit-border-radius: 0 5px 5px 0;
    -moz-border-radius: 0 5px 5px 0;
    -ms-border-radius: 0 5px 5px 0;
    -o-border-radius: 0 5px 5px 0;
    overflow: hidden;
    display: flex;
    align-items: center;
}
.menu__left {
    min-width: 90px;
    max-width: 90px;
    display: grid;
    justify-content: center;
}
.menu__right {
    width: 230px;
    display: grid;
    height: calc(100% - 90px);
    align-items: start;
    align-content: flex-start;
}
.menu__right a {
    width: 190px !important;
    display: flex;
    border-bottom: none !important;
}
.menu__right a i {
    margin-right: 10px;
}
.active { 
    color: #0d6efd !important;
}
.dashboard a{
    width: 60px;
    text-align: center;
    text-decoration: none;
    color: #000;
    transition: width 0.15s;
    -webkit-transition:width 0.15s;
    -moz-transition:width 0.15s;
    -ms-transition:width 0.15s;
    -o-transition:width 0.15s;
}
.dashboard a {
    border-bottom: 1px solid #ced4da;
}
.dashboard a:last-child {
    border-bottom: none;
}
.dashboard a:hover {
    color:#0d6efd;
    transition: width 0.15s;
    -webkit-transition: width  0.15s;
    -moz-transition:width 0.15s;
    -ms-transition:width 0.15s;
    -o-transition:width 0.15s;
}
.dashboard_line {
    display: block;
    width: 1px;
    background: #ced4da;
    height: 100%;
}
.linking {
    position: relative;
}
.cartLength {
    position: absolute;
    right: 0px;
    top: 10px;
}
.bookLength {
    position: absolute;
    right: -5px;
    top: 10px;
}
.wrapper {
    padding: 80px 20px 20px 20px;
}
.card {
    padding: 0 !important;
}
.itemButtons {
    padding: 0 !important;
}
.itemButtons button {
    margin-left: 10px;
}
.comments_leng {
    position: relative;

}
.comments_leng span {
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 15px;
    left: 0;
    top: 0;
    font-size: 10px;
    position: absolute;
    margin-top: 4px;
}

.comment_profile {
    
}
.comment_profile i {
    font-size: 55px;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    line-height: 0;
}
.comment_profile img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.smallWrap {
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    display: grid;
    align-items: start;
    align-content: flex-start;
}
.wrapper-color {
  background: #fff;
  margin-top: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 12px 0px rgb(0 0 0 / 49%);
}
